import React from 'react';
import { connect } from 'react-redux';

import { MuiThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid/Grid';

import { withLocalize } from 'react-localize-redux';
import siteTranslations from '../translations/site.json';

import AOS from 'aos';
import 'aos/dist/aos.css';

import { showSnackbar } from '../actions/snackbar';

import customTheme from './customTheme';

import { setScreen } from '../actions/app';

AOS.init({
  once: true, // whether animation should happen only once - while scrolling down
  mirror: false, // whether elements should animate out while scrolling past them
});

class Footer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};
    this.props.addTranslation(siteTranslations);
  }

  render() {
    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="footer">
          <Grid container spacing={24}>
            <Grid item xs={12} md={12} lg={12}>
              <span className="glowing-pink">
                &copy; 2019 TronLegend{' '}
                <span className="social">
                  <a
                    href="https://twitter.com/TronLegendGames"
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    <img
                      src="images/social/twitter.png"
                      className="image-flag"
                      alt="Twitter"
                    />
                  </a>
                </span>
                <span className="social">
                  <a
                    href="https://medium.com/@tronlegend"
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    <img
                      src="images/social/medium.png"
                      className="image-flag"
                      alt="Medium"
                    />
                  </a>
                </span>
                <span className="social">
                  <a
                    href="https://www.youtube.com/channel/UCuPklhFNpN0r2ZACGy8hSBA"
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    <img
                      src="images/social/youtube.png"
                      className="image-flag"
                      alt="Youtube"
                    />
                  </a>
                </span>
              </span>
            </Grid>
            <Grid item xs={12} md={12} lg={12} />
          </Grid>
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapDispatchToProps = {
  showSnackbar,
  setScreen,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(Footer));
